      var results = res.data.results
      var lists = [];
      for (var i = 0; i < results.length; i++){
        var keys = Object.keys(results[i].properties)
        dispatch(requestNotionUserScore(results[i].properties.이름.title[0].plain_text))
        var data = {
          병실 : results[i].properties[keys[9]].number,
          등록번호 : results[i].properties[keys[4]].number,
          이름 : results[i].properties.이름.title[0].plain_text,
          성별 : results[i].properties.성별.select.name,
          나이 : results[i].properties.나이.number,
          입원과 : results[i].properties.입원과.rich_text[0].plain_text,
          주치의 : results[i].properties.주치의.rich_text[0].plain_text,
          입원일 : results[i].properties.입원일.rich_text[0].plain_text,
          hd : results[i].properties.HOD.number,
          진단명 : results[i].properties.진단명.rich_text[0].plain_text,
        }
        lists.push(data)
      }
      dispatch(loadingFalse())
      return lists
