export const requestNotionAuth = (id, pw) => {
  return async (dispatch) => {
    dispatch(loadingTrue())
    axios.get(`https://lazenka.yeriel-ryu0124.workers.dev/auth?id=${id}&pw=${pw}`)
    .then(res=>{
      dispatch(loadingFalse())
      var results = res.data.results;
      if (results.length !== 0) {
        dispatch(loginTrue());
        localStorage.setItem('isAccess', true);
        window.open('/DeepSaver/main', 'LAZENKA', 'width=' + window.screen.width + ',height=' + window.screen.height + ',fullscreen=yes');
      } else {
        window.alert('회원 정보가 없습니다.')
      }
    })
  }
};

export const requestNotionSettingTime = (name) => {
  return async (dispatch) => {
    axios.get(`https://lazenka.yeriel-ryu0124.workers.dev/setting?name=${name}`)
    .then(res=>{
      var results = res.data.results[0].properties.settingTime.number
      console.log(results)
      var data = {
        settingTime: parseInt(results),
        page_id: res.data.results[0].id
      }
      dispatch(settingTime(data))
    })
    .catch(err=>console.log(err))
  }
};

export const requestNotionUpdateTime = (name) => {
  return async (dispatch) => {
    axios.get(`https://lazenka.yeriel-ryu0124.workers.dev/update?name=${name}`)
    .then(res=>{
      var results = res.data.results[0].properties.updateTime.date.start
      var dateTime = new Date(results)
      var data = {
        updateTime: dateTime.toLocaleString(),
        page_id:res.data.results[0].id
      }
      dispatch(updateTime(data))
    })
    .catch(err=>console.log(err))
  }
};

export const requestNotionPatchTime = (page_id, type, set) => {
  return async (dispatch) => {
    axios.get(`https://lazenka.yeriel-ryu0124.workers.dev/timesetting?page=${page_id}&type=${type}&set=${set}`)
    .then(res => {
      var results = res.data.properties
      if (type === 'settingTime') {
        console.log(type, results)
        var data = {
          settingTime: results.settingTime.number,
          page_id: res.data.id
        }
        dispatch(settingTime(data))
      } else if (type==='updateTime') {
        console.log(type, results)
        var dateTime = new Date(results.updateTime.date.start)
        console.log(dateTime)
        console.log(dateTime.toLocaleString())
        var da = {
          updateTime: dateTime.toLocaleString(),
          page_id:res.data.id
        }
        dispatch(updateTime(da))
      }
    })
    .catch(err => console.log(err))
  }
};

const initialState = {
  users: [],
  score: [],
  auth: false,
  settingTime: {
    page_id: '',
    settingTime: 2
  },
  updateTime: {
    updateTime: '-.-.-. --:--',
    page_id: ''
  }
};

const user_list = (state, action) => ({
  ...state,
  users: action.lists
});

const user_score = (state, action) => ({
  ...state,
  score: state.score.concat(action.score)
});

const score_reset = (state, action) => ({
  ...state,
  score: []
});

const login_true = (state, action) => ({
  ...state,
  login: true
});

const setting_time = (state, action) => ({
  ...state,
  settingTime: action.time
});

const update_time = (state, action) => ({
  ...state,
  updateTime: action.time
});

const user = (state=initialState, action) => {
  switch (action.type) {
    case USER_LIST:
      return user_list(state, action)
    case USER_SCORE:
      return user_score(state, action)
    case SCORE_RESET:
      return score_reset(state, action)
    case LOGIN_TRUE:
      return login_true(state, action)
    case SETTING_TIME:
      return setting_time(state, action)
    case UPDATE_TIME:
      return update_time(state, action)
    default:
      return state;
  }
};

export default user;