import React from 'react';
import { Icon } from '../../components/Template/Template';
import AlertList from '../../components/AlertList';
import Download from '../../components/Icons/Download';
import PlayOutline from '../../components/Icons/PlayOutline';
import Setting from '../../components/Icons/Setting';
import Loading from '../../components/Loading/Loading';
import PatientPage from '../../components/PatientPage';
import { ListItemIcon, ListItemText, Menu, MenuItem, Dialog, DialogTitle, DialogContent, Checkbox, FormGroup, FormControlLabel } from '@mui/material';
import styled, { ThemeProvider } from 'styled-components';

const Container = styled.div`
  padding: 20px;
  background-color: ${props => props.theme.bg};
  color: ${props => props.theme.color};
`;
const FlexDiv = styled.div`
  display: flex;
  justify-content: space-between;
  font-size: 17px;
  align-items: center;
`;
const SelectFlexDiv = styled(FlexDiv)`
  gap: 30px;
`;
const ContentFlexDiv = styled(FlexDiv)`
  background-color:  ${props => props.theme.menu};
  border-radius: 20px;
  border: 1px solid transparent;
  gap: 25px;
  background-image: ${props => props.theme.bi};
  background-origin: border-box;
  background-clip: content-box, border-box;
  height: 41px;
  color: ${props => props.theme.color};
`;
const Alert = styled.div`
  border-radius: 50%;
  width: 15px;
  height: 15px;
  background-color: ${props=>props.color};
`;
const SelectContainer = styled.div`
  width: 150px;
  height: 41px;
  border: 1px solid transparent;
  background-image: linear-gradient(#fff, #fff), linear-gradient(90deg, #7F47DC, rgb(21, 40, 160), #12279E);
  background-origin: border-box;
  background-clip: content-box, border-box;
  border-radius: 20px;
`;
const Select = styled.select`
  width: 100%;
  height: 100%;
  font-size: 17px;
  border-radius: 20px;
  border: none;
  text-indent: 10px;
  appearance: none;
  background: url('./chevron--down.svg') calc(100% - 7px) center no-repeat;
  background-size: 20px;
`;
const Divider = styled.hr`
  height: 2px;
  border-style: none;
  background: linear-gradient(90deg, #7F47DC, rgb(21, 40, 160), #12279E);
`;
const TitleContainer = styled(FlexDiv)`
  background-color:  ${props => props.theme.menu};
  margin: 20px 0;
  padding: 0px 20px;
  color: white;
`;
const Title = styled.p`
  font-size: 17px;
  font-weight: bold;
  letter-spacing: 2px;
`;
const Table = styled.table`
  background-color: ${props => props.theme.table};
  width: -webkit-fill-available;
  border-spacing: 0;
`;
const TableHead = styled.th`
  padding: 10px 10px;
  background-color: ${props => props.theme.head};
  font-size: 17px;
  font-weight: 700;
  color: white;
`;
const TableBody = styled.tbody`
  display: block;
  height: 206px;
  overflow-y: scroll;
  width: -webkit-fill-available;
`;
const TableRow = styled.tr`
  cursor: pointer;
  color: ${props => props.theme.color};
  width: -webkit-fill-available;
  &:hover {
    background-color: ${props => props.theme.hover};
  }
  &:nth-child(even) {
    background-color: ${props => props.theme.row};
    &:hover {
    background-color: ${props => props.theme.hover};
  }
  }
`;
const TableCell = styled.td`
  text-align: center;
  padding: 10px 0;
  width: ${props=>props.width};
`;
const ColorDiv = styled.div`
  width: 17px;
  height: 17px;
  background-color: ${props=>props.bgcolor};
  margin-right: 4px;
  border-radius: 50%;
`;
const StyleMenu = styled(Menu)`
  div.MuiPaper-root-MuiMenu-paper-MuiPaper-root-MuiPopover-paper {
    color: white;
    background-color: black;
  };
  div>ul>li {
    &:hover{
      background-color: gray;
    }
  }
`;
const StyledDialog = styled(Dialog)`
  div.MuiPaper-root-MuiDialog-paper {
    background-color: rgb(45, 50, 65);
    color: white;
    border: 1px solid #333333;
  }
`;
const StyledCheckbox = styled(FormControlLabel)`
  svg {
    fill: gray;
  }
  color: gray;
`;

const ScoreLevel = (score) => {
  if (score < 34 ) return <ColorDiv bgcolor='#009145' /> ;
  else if (score >= 34 && score < 68) return <ColorDiv bgcolor='#FB9317' />;
  else return <ColorDiv bgcolor='#FF0000' />
};

const MainPresenter = (props) => {
  const {
    theme, 
    loading,
    userList,
    scoreList,
    model1list, 
    model2list, 
    model3list, 
    model4list,
    onColorFun, 
    redCount,
    yellowCount, 
    greenCount,
    ward,
    department,
    attending,
    onWardChange,
    onDepartmentChange,
    onAttendingChange,
    alertOpen,
    onAlertListOpen,
    patientPageOpen,
    onClosePatientPage,
    onOpenPatientPage,
    modelMenuOn,
    onOpenModelMenu,
    onCloseModelMenu,
    modelSettingModal,
    onModelSettingToggle,
    modelSetting,
    onChecked,
    userId
  } = props;
  return (
    <ThemeProvider theme={theme}>
      {loading ? <Loading /> : null}
      <Container>
        <FlexDiv style={{marginBottom: '20px'}}>
          <SelectFlexDiv>
            <SelectContainer>
              <Select value={ward} onChange={onWardChange}>
                <option value='none'>병동</option>
                <option value="10">10</option>
                <option value="11">11</option>
                <option value="12">12</option>
                <option value="13">13</option>
              </Select>
            </SelectContainer>
            <SelectContainer>
              <Select value={department} onChange={onDepartmentChange}>
                <option value="none">입원과</option>
                <option value="CS">CS</option>
                <option value="GS">GS</option>
                <option value="MC">MC</option>
                <option value="MD">MD</option>
                <option value="MP">MP</option>
                <option value="NR">NR</option>
                <option value="OS">OS</option>
              </Select>
            </SelectContainer>
            <SelectContainer>
              <Select value={attending} onChange={onAttendingChange}>
                <option value="none">주치의</option>
                <option value="강민섭">강민섭</option>
                <option value="강희상">강희상</option>
                <option value="김민우">김민우</option>
                <option value="김윤혁">김윤혁</option>
                <option value="김윤석">김윤석</option>
                <option value="김지후">김지후</option>
                <option value="이제석">이제석</option>
              </Select>
            </SelectContainer>
          </SelectFlexDiv>
          <ContentFlexDiv>
            <div style={{width:'1px'}} />
            <div>총 관리 환자 {userList.length}명</div>
            <FlexDiv style={{gap:'20px'}}>
              <FlexDiv style={{gap:'10px'}}>
                <Alert color='#ff0000' /> {redCount}명
              </FlexDiv>
              <FlexDiv style={{gap:'10px'}}>
                <Alert color='#FB9317' /> {yellowCount}명
              </FlexDiv>
              <FlexDiv style={{gap:'10px'}}>
                <Alert color='#009145' /> {greenCount}명
              </FlexDiv>
            </FlexDiv>
            <Icon><Download size={20} color='white' /></Icon>
            <div style={{width:'1px'}} />
          </ContentFlexDiv>
        </FlexDiv>
        <Divider />
        <TitleContainer>
          <Title>예측 시스템</Title>
          <Icon onClick={onOpenModelMenu}><Setting size={30} color='white' /></Icon>
        </TitleContainer>
        <StyleMenu open={modelMenuOn} onClose={onCloseModelMenu} anchorEl={modelMenuOn}>
          <MenuItem>
            <ListItemIcon><PlayOutline size={25} color='white' /></ListItemIcon>
            <ListItemText>실행</ListItemText>
          </MenuItem>
          <MenuItem onClick={onModelSettingToggle}>
            <ListItemIcon><Setting size={25} color='white' /></ListItemIcon>
            <ListItemText>Model 설정</ListItemText>
          </MenuItem>
        </StyleMenu>
        <FlexDiv style={{gap: '3px'}}>
          {modelSetting.gbt ? (
            <Table>
              <thead>
                <tr><TableHead colSpan={3}>GBT</TableHead></tr>
              </thead>
              <TableBody>
                {model1list.length !== 0 && model1list.map(list=> {
                  var color = onColorFun(list.model1)
                  return (
                  <TableRow key={list.name}>
                    <TableCell width={'1%'}><Alert color={color} style={{marginLeft: '20px'}}/></TableCell>
                    <TableCell>{list.name}</TableCell>
                    <TableCell>{list.model1}</TableCell>
                  </TableRow>
                )})}
              </TableBody>
            </Table>
          ) : null}
          {modelSetting.model2 ? (
            <Table>
              <thead>
                <tr><TableHead colSpan={3}>Model2</TableHead></tr>
              </thead>
              <TableBody>
                {model2list.length !== 0 && model2list.map(list=> (
                  <TableRow key={list.name}>
                    <TableCell width='1%'><Alert color={onColorFun(list.model2)} style={{marginLeft: '20px'}}/></TableCell>
                    <TableCell>{list.name}</TableCell>
                    <TableCell>{list.model2}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          ): null}
          {modelSetting.model3 ? (
            <Table>
              <thead>
                <tr><TableHead colSpan={3}>Model3</TableHead></tr>
              </thead>
              <TableBody>
                {model3list.length !== 0 && model3list.map(list=> (
                  <TableRow key={list.name}>
                    <TableCell width='1%'><Alert color={onColorFun(list.model3)} style={{marginLeft: '20px'}}/></TableCell>
                    <TableCell>{list.name}</TableCell>
                    <TableCell>{list.model3}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          ): null}
          {/* <Table>
            <thead>
              <tr><TableHead colSpan={3}>Model4</TableHead></tr>
            </thead>
            <TableBody>
              {model4list.length !== 0 && model4list.map(list=> (
                <TableRow key={list.name}>
                  <TableCell width='5%'><Alert color={onColorFun(list.model4)} style={{marginLeft: '20px'}}/></TableCell>
                  <TableCell>{list.name}</TableCell>
                  <TableCell>{list.model4}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table> */}
        </FlexDiv>
        <TitleContainer>
          <Title>환자 리스트</Title>
          <Icon><Setting size={30} color='white' /></Icon>
        </TitleContainer>
        <Table>
          <thead>
            <tr>
              <TableHead>병실</TableHead>
              <TableHead>위험도예측</TableHead>
              <TableHead>등록번호</TableHead>
              <TableHead>이름</TableHead>
              <TableHead>성별</TableHead>
              <TableHead>나이</TableHead>
              <TableHead>입원과</TableHead>
              <TableHead>주치의</TableHead>
              <TableHead>입원일</TableHead>
              <TableHead>HD</TableHead>
              <TableHead>진단명</TableHead>
            </tr>
          </thead>
          <tbody style={{height: '100%'}}>
            { userList.length !== 0 && userList.map(list=> (
              <TableRow key={list.등록번호} onClick={onOpenPatientPage} data-name={list.등록번호}>
                <TableCell width='8%'>{list.병실}</TableCell>
                <TableCell style={{display:'flex', justifyContent:'center'}}>
                  {scoreList.filter(score => score.name===list.이름).length !== 0 && ScoreLevel(scoreList.filter(score => score.name===list.이름)[0].model1)}
                </TableCell>
                <TableCell width='8%'>{list.등록번호}</TableCell>
                <TableCell width='5%'>{list.이름}</TableCell>
                <TableCell width='5%'>{list.성별}</TableCell>
                <TableCell width='5%'>{list.나이}</TableCell>
                <TableCell>{list.입원과}</TableCell>
                <TableCell>{list.주치의}</TableCell>
                <TableCell>{list.입원일}</TableCell>
                <TableCell width='5%'>{list.hd === '' ?'-':list.hd}</TableCell>
                <TableCell>{list.진단명}</TableCell>
              </TableRow>
            ))}
          </tbody>
        </Table>
      </Container>
      <AlertList open={alertOpen} onClose={onAlertListOpen} />
      <PatientPage open={patientPageOpen} onClose={onClosePatientPage} id={userId}/>
      <StyledDialog open={modelSettingModal} onClose={onModelSettingToggle} maxWidth='sm' fullWidth>
        <DialogTitle style={{fontWeight:'bolder', color:'#7F47DC'}}>Model Setting</DialogTitle>
        <DialogContent>
          <FormGroup>
            <StyledCheckbox control={<Checkbox checked={modelSetting.gbt} name='gbt' onChange={onChecked} />} label='GBT' />
            <StyledCheckbox control={<Checkbox checked={modelSetting.model2} name='model2' onChange={onChecked} />} label='Model2' />
            <StyledCheckbox control={<Checkbox checked={modelSetting.model3} name='model3' onChange={onChecked} />} label='Model3' />
          </FormGroup>
        </DialogContent>
        {/* <DialogActions><Button variant='contained' style={{backgroundColor:'#7F47DC'}} >SAVE</Button></DialogActions> */}
      </StyledDialog>
    </ThemeProvider>
  );
};

export default MainPresenter;