import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { requestNotionPatchTime, requestNotionSettingTime, requestNotionUpdateTime } from '../../moduels/user';
import PatientPagePresenter from './PatientPagePresenter';

const PatientPageContainer = ({open, id, onClose}) => {
  const dispatch = useDispatch();

  const [fullScreen, setFullScreen] = useState(false);
  const [labOpen, setLabOpen] = useState(false);
  const [chartOpen, setChartOpen] = useState(false)
  // const [settingTime, setSettingTime] = useState(2);
  // const [updateTime, setUpdateTime] = useState(new Date());

  const updateTime = useSelector(state=>state.user.updateTime);
  const settingTime = useSelector(state=>state.user.settingTime);

  useEffect(()=>{
    if (id === '2205173') {
      dispatch(requestNotionSettingTime('김말자'))
      dispatch(requestNotionUpdateTime('김말자'))
    } else {
      dispatch(requestNotionSettingTime('정시영'))
      dispatch(requestNotionUpdateTime('정시영'))
    }
  },[dispatch, id]);

  const data = [
    {
      name: '2022.09.12T09:00',
      맥박: 68,
      호흡수: 20,
      체온: 36.4,
      이완기: 128,
      수축기: 80,
      Sp02: 95,
    },
    {
      name: '2022.09.12T10:00',
      맥박: 60,
      호흡수: 20,
      체온: 36.8,
      이완기: 120,
      수축기: 78,
    },
    {
      name: '2022.09.12T11:00',
      맥박: 80,
      호흡수: 23,
      체온: 37.6,
      이완기: 130,
      수축기: 100,
      Sp02: 95,
    },
    {
      name: '2022.09.12T12:00',
      맥박: 89,
      호흡수: 26,
      체온: 37.8,
      이완기: 129,
      수축기: 96,
      Sp02: 95,
    },
    {
      name: '2022.09.12T13:00',
      맥박: 70,
      호흡수: 20,
      체온: 37.1,
      이완기: 126,
      수축기: 80,
      Sp02: 95,
    },
    {
      name: '2022.09.12T14:00',
      맥박: 67,
      호흡수: 20,
      체온: 36.4,
      이완기: 117,
      수축기: 80,
    },
    {
      name: '2022.09.12T15:00',
      맥박: 72,
      호흡수: 20,
      체온: 36.4,
      이완기: 117,
      수축기: 80,
      Sp02: 95
    },
  ];

  const onFullscreenChange = () => {
    setFullScreen(prev=> !prev);
  };
  const onLabOpen = () => {
    setLabOpen(prev => !prev);
  };
  const onChartOpen = () => {
    setChartOpen(prev => !prev)
  };

  const onSettingTime = (e) => {
    var now = new Date()
    now = now.toLocaleString('sv', {year:'numeric', month:'numeric', day:'numeric', hour:'numeric', minute:'numeric', second:'numeric', fractionalSecondDigits: 3}).replace(',', '.').replace(' ', 'T');
    dispatch(requestNotionPatchTime(settingTime.page_id, 'settingTime', e.target.value))
    dispatch(requestNotionPatchTime(updateTime.page_id, 'updateTime', now))
  };

  return (
    <PatientPagePresenter 
      open={open} 
      onClose={onClose}
      fullScreen={fullScreen}
      onFullscreenChange={onFullscreenChange}
      data={data}
      labOpen={labOpen}
      chartOpen={chartOpen}
      onLabOpen={onLabOpen}
      onChartOpen={onChartOpen}
      id={id}
      settingTime={settingTime.settingTime}
      updateTime={updateTime.updateTime}
      onSettingTime={onSettingTime}
    />
  );
};

export default PatientPageContainer;