export const THEME_CHANGE = 'layout/THEME_CHANGE';
export const LOADING_TRUE = 'layout/LOADING_TRUE';
export const LOADING_FALSE = 'layout/LOADING_FALSE';

export const themeChange = () => ({
  type: THEME_CHANGE
});
export const loadingTrue = () => ({
  type: LOADING_TRUE
});
export const loadingFalse = () => ({
  type: LOADING_FALSE
});

const initialState = {
  theme: true, // true: dark, false: light
  loading: false
};

const theme_change = (state, action) => ({
  ...state,
  theme: !state.theme
});

const loading_true = (state, action) => ({
  ...state,
  loading: true
});
const loading_false = (state, action) => ({
  ...state,
  loading: false
});

const layout = (state=initialState, action) => {
  switch (action.type) {
    case THEME_CHANGE:
      return theme_change(state, action)
    case LOADING_TRUE:
      return loading_true(state, action)
    case LOADING_FALSE:
      return loading_false(state, action)
    default:
      return state;
  }
};

export default layout;
