import React, { useCallback, useState } from 'react';
import { useDispatch } from 'react-redux';
import { requestNotionAuth } from '../../moduels/user';
import LoginPresenter from './LoginPresenter';

const LoginContainer = () => {
  // const [isAccess, setIsAccess] = useState(false)

  const dispatch = useDispatch();
  const [user, setUser] = useState({id:'', pw:''})
  const isAccess = localStorage.getItem('isAccess')

  const onChange = useCallback((e) => {
    var {name, value} = e.target;
    setUser({...user, [name]: value});
  },[user])
  
  const onLogin = () => {
    // if (user.id === 'lazenca' && user.pw === 'cosmosmedic') {
    //   window.open('/main', 'LAZENKA', 'width=' + window.screen.width + ',height=' + window.screen.height + ',fullscreen=yes');
    //   setIsAccess(true);
    // } 
    dispatch(requestNotionAuth(user.id, user.pw))
  }

  return (
    <LoginPresenter 
      isAccess={isAccess}
      user={user}
      onLogin={onLogin}
      onChange={onChange}
    />
  );
};

export default LoginContainer;