export const LazenkaAlert = React.forwardRef(({model, user, score, onClose}, ref) => {
  const [expand, setExpand] = useState(false)
  const onExpand = () => {
    setExpand(prev=>!prev)
  };
  return (
    <Alert onClose={onClose} icon={false} ref={ref} style={{backgroundColor:'#cccccc'}}>
      <Container>
      <ScoreDiv bgcolor="#ff0000">
          <div style={{fontSize:'15px'}}>{'shock'}</div>
          <div>{10}</div>
        </ScoreDiv>
        <ContentContainer>
          <DateTime>2022년 9월 13일 09:18 AM</DateTime>
          <UserData>10118 홍길동 M/34</UserData>
          <ExpandContainer>
            <ReasonText>V/S : MAP &lt; 65 </ReasonText>
            <Icon expand={expand} onClick={onExpand}><ChevronDown size='15' /></Icon>
          </ExpandContainer>
        </ContentContainer>
      </Container>
      <PrevContainer expand={expand}>
        <PrevAlarm>
          <DateTime>2022년 9월 13일 09:00 AM (13분전)</DateTime>
          <ReasonText>V/S : MAP &lt; 65</ReasonText>
        </PrevAlarm>
        <PrevAlarm>
          <DateTime>2022년 9월 13일 09:00 AM (13분전)</DateTime>
          <ReasonText>V/S : MAP &lt; 65</ReasonText>
        </PrevAlarm>
      </PrevContainer>
    </Alert>
  )
});

