import React, { useEffect, useState } from 'react';
import { useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Dark, Light } from '../../components/Template/Theme';
import { alertChange } from '../../moduels/alert';
import { requestNotionUserScore, requetNotionFilterUserList, requetNotionUserList } from '../../moduels/user';

import MainPresenter from './MainPresenter';

const MainContainer = () => {
  const dispatch = useDispatch();

  const themeType = useSelector(state=>state.layout.theme);
  const alertOpen = useSelector(state=>state.alert.open);
  const userList = useSelector(state=>state.user.users);
  const scoreList = useSelector(state=>state.user.score);
  const loading = useSelector(state=>state.layout.loading);

  const [theme, setTheme] = useState(Dark);
  const [redCount, setRedCount] = useState(0);
  const [yellowCount, setYellowCount] = useState(0);
  const [greenCount, setGreenCount] = useState(0);

  const [ward, setWard] = useState('none');
  const [department, setDepartment] = useState('none');
  const [attending, setAttending] = useState('none');


  const [patientPageOpen, setPatientPageOpen] = useState(false);
  const [modelMenuOn, setModelMenuOn] = useState(null);
  const [modelSettingModal, setModelSettingModal] = useState(false);
  const [modelSetting, setModelSetting] = useState({gbt:true, model2: false, model3: false});
  const [userId, setUserId] = useState(2108029)
  // useEffect(()=>{
  //   setTheme(themeType ? Dark : Light);
  // },[themeType]);

  useEffect(()=> {
    setRedCount(0)
    setYellowCount(0)
    setGreenCount(0)
    dispatch(requetNotionUserList())
  },[dispatch])

  useEffect(()=> {
    if (scoreList.length === userList.length) {
      for (var i = 0; i < scoreList.length; i++) {
        if (scoreList[i].model1 < 34) {
          setGreenCount(prev => prev + 1)
        } else if (scoreList[i].model1 >= 34 && scoreList[i].model1 < 68) {
          setYellowCount(prev => prev + 1)
        } else if (scoreList[i].model1 < 100 && scoreList[i].model1 >= 68) {
          setRedCount(prev => prev + 1)
        }
        if (scoreList[i].model2 < 34) {
          setGreenCount(prev => prev + 1)
        } else if (scoreList[i].model2 >= 34 && scoreList[i].model2 < 68) {
          setYellowCount(prev => prev + 1)
        } else if (scoreList[i].model2 < 100 && scoreList[i].model2 >= 68) {
          setRedCount(prev => prev + 1)
        }
        if (scoreList[i].model3 < 34) {
          setGreenCount(prev => prev + 1)
        } else if (scoreList[i].model3 >= 34 && scoreList[i].model3 < 68) {
          setYellowCount(prev => prev + 1)
        } else if (scoreList[i].model3 < 100 && scoreList[i].model3 >= 68) {
          setRedCount(prev => prev + 1)
        }
        if (scoreList[i].model4 < 34) {
          setGreenCount(prev => prev + 1)
        } else if (scoreList[i].model4 >= 34 && scoreList[i].model4 < 68) {
          setYellowCount(prev => prev + 1)
        } else if (scoreList[i].model4 < 100 && scoreList[i].model4 >= 68) {
          setRedCount(prev => prev + 1)
        }
      }
    }
  }, [scoreList, userList]);

  const onModelSort = (a, b, ind) => {
    const numA = a[ind];
    const numB = b[ind];
    if (numA < numB) {
      return 1;
    }
    if (numA > numB) {
      return -1;
    }
    return 0;
  };
  const onColorFun = (num) => {
    if (num < 34) {
      return '#009145'
    } else if (num >=34 && num < 68) {
      return '#fb9317'
    } else {
      return '#ff0000'
    }
  }
  var model1list = [...scoreList].sort((a, b)=>onModelSort(a,b,'model1'));
  var model2list = [...scoreList].sort((a, b)=>onModelSort(a,b,'model2'));
  var model3list = [...scoreList].sort((a, b)=>onModelSort(a,b,'model3'));
  var model4list = [...scoreList].sort((a, b)=>onModelSort(a,b,'model4'));

  const onWardChange = (e) => {
    setWard(e.target.value);
    dispatch(requetNotionFilterUserList(e.target.value, department, attending));
    setRedCount(0)
    setYellowCount(0)
    setGreenCount(0)
  };
  
  const onDepartmentChange = (e) => {
    setDepartment(e.target.value);
    dispatch(requetNotionFilterUserList(ward, e.target.value, attending));
    setRedCount(0)
    setYellowCount(0)
    setGreenCount(0)
  };
  
  const onAttendingChange = (e) => {
    setAttending(e.target.value);
    dispatch(requetNotionFilterUserList(ward, department, e.target.value));
    setRedCount(0)
    setYellowCount(0)
    setGreenCount(0)
  };

  const onAlertListOpen = () => {
    dispatch(alertChange())
  };

  const onClosePatientPage = () => {
    setPatientPageOpen(false)
  };

  const onOpenPatientPage = (e) => {
    // console.log(e.currentTarget.dataset)
    setPatientPageOpen(true)
    var patientName = e.currentTarget.dataset.name;
    setUserId(patientName)
  };

  const onOpenModelMenu = (e) => {
    setModelMenuOn(e.currentTarget)
  };
  const onCloseModelMenu = () => {
    setModelMenuOn(null)
  };
  const onModelSettingToggle = () => {
    setModelSettingModal(prev=>!prev);
    onCloseModelMenu()
  };
  const onChecked = useCallback((e) => {
    setModelSetting({...modelSetting, [e.target.name]:!modelSetting[e.target.name]})
  },[modelSetting]);

  return (
    <MainPresenter 
      theme={theme} 
      loading={loading}
      userList={userList}
      scoreList={scoreList}
      model1list={model1list}
      model2list={model2list}
      model3list={model3list}
      model4list={model4list}
      onColorFun={onColorFun}
      redCount={redCount}
      yellowCount={yellowCount}
      greenCount={greenCount}
      ward={ward}
      department={department}
      attending={attending}
      onWardChange={onWardChange}
      onDepartmentChange={onDepartmentChange}
      onAttendingChange={onAttendingChange}
      alertOpen={alertOpen}
      onAlertListOpen={onAlertListOpen}
      patientPageOpen={patientPageOpen}
      onClosePatientPage={onClosePatientPage}
      onOpenPatientPage={onOpenPatientPage}
      modelMenuOn={modelMenuOn}
      onOpenModelMenu={onOpenModelMenu}
      onCloseModelMenu={onCloseModelMenu}
      modelSettingModal={modelSettingModal}
      onModelSettingToggle={onModelSettingToggle}
      modelSetting={modelSetting}
      onChecked={onChecked}
      userId={userId}
    />
  );
};

export default MainContainer;