export const ALERT_CHANGE = 'alert/ALERT_CHANGE';

export const alertChange = () => ({
  type: ALERT_CHANGE
});

const initialState = {
  open: false
};

const alert_change = (state, action) => ({
  ...state,
  open: !state.open
});

const alert = (state=initialState, action) => {
  switch (action.type) {
    case ALERT_CHANGE:
      return alert_change(state, action)
  
    default:
      return state;
  }
};

export default alert;